.usersBlock {
    max-width: 100vw;
    .flexUsersWrap {
        display: flex;
        flex-wrap: wrap;
        gap: 15px;
        justify-content: center;
        align-items: center;
        padding: 15px;

        .userCard {
            flex: 0 1 320px;
            box-shadow: 0 -6px 13px rgba(0, 0, 0, 0.8), 0 5px 10px rgba(0, 0, 0, 0.5);
            border-radius: 10px;

            .userWrap {
                background-color: #b3f1de;
                display: flex;
                flex-direction: column;
                min-height: 450px;

                .cardContent {
                    flex: 1;

                    .userName {
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        flex: 1 1 auto;
                    }
                }

                .wrapBtn {
                    border: 1px solid;
                    background-color: #4343a4;
                    transition: background-color .3s ease-in-out;

                    button {
                        color: #b9d7f6;
                    }

                    &:hover {
                        background-color: #5858c0;
                    }
                }
            }
        }
    }

}



